
"use client";

import * as React from "react";
import { useEffect, useState } from "react";
import Link from "next/link";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { ArrowRight, Loader2 } from "lucide-react";
import { useUser, useAuth } from "@/firebase";
import { initiateAnonymousSignIn } from "@/firebase/non-blocking-login";
import AdBanner from "../AdBanner";
import placeholderData from "@/lib/placeholder-images.json";

export function SplashScreen() {
  const { user, isUserLoading } = useUser();
  const auth = useAuth();
  const [isSigningIn, setIsSigningIn] = useState(false);
  
  const throneImage = placeholderData.placeholderImages.find(p => p.id === "splash-throne");

  // Automatically sign in anonymously if not logged in
  useEffect(() => {
    if (!isUserLoading && !user && auth) {
      setIsSigningIn(true);
      initiateAnonymousSignIn(auth);
    } else if (!isUserLoading && user) {
        setIsSigningIn(false);
    }
  }, [isUserLoading, user, auth]);

  const renderContent = () => {
    // Show loader while checking auth state or while the user object is being populated after sign-in
    if (isUserLoading || isSigningIn || !user) {
      return (
        <div className="flex flex-col items-center gap-2">
            <Loader2 className="h-12 w-12 animate-spin text-primary" />
            <p className="text-sm text-muted-foreground">Awakening the Fartmaster...</p>
        </div>
      )
    }

    // Once user object is available, show the "Begin" button
    if (user) {
      return (
        <Button asChild size="lg" className="animate-pulse bg-accent text-accent-foreground hover:bg-accent/90 text-lg font-bold">
          <Link href="/character-creation">
            Begin Your Journey <ArrowRight className="ml-2" />
          </Link>
        </Button>
      );
    }
    
    // Fallback, though should be covered by the loading state
    return <Loader2 className="h-12 w-12 animate-spin text-primary" />;
  };

  return (
    <div className="relative flex flex-col items-center justify-center min-h-screen text-center overflow-hidden p-4">
      {throneImage && (
         <Image
            src={throneImage.imageUrl}
            alt={throneImage.description}
            fill
            className="object-cover -z-10"
            data-ai-hint={throneImage.imageHint}
            unoptimized
            priority
        />
      )}
      <div className="absolute inset-0 bg-black/40 -z-10" />

      <AdBanner adSlot="YYYYYYYYYY" className="absolute top-4 left-1/2 -translate-x-1/2 z-20" />
      
      <div className="relative z-10 flex flex-col items-center">
        <h1 className="font-headline text-7xl md:text-8xl lg:text-9xl font-bold text-glow mb-2 tracking-wider">
          Poot Quest
        </h1>
        <p className="font-body text-xl md:text-2xl text-primary/80 mb-12 italic">
          The Land of Never-Ending Asses
        </p>
        <div className="min-h-[76px] flex items-center justify-center">
          {renderContent()}
        </div>
      </div>

      <AdBanner adSlot="XXXXXXXXXX" className="absolute bottom-4 left-1/2 -translate-x-1/2 z-20" />
    </div>
  );
}
